    import {
        mapState,mapMutations
    } from 'vuex';
	export default {
		data() {
			return {
				source: 0,

			}
		},
		onLoad(option){
			console.log(option.source);
			this.source = option.source;
			this.initAddress()
			console.log("addressList")
			console.log(this.addressList)
			console.log(this.userInfo)


		},
        computed:{
            ...mapState(['addressList','userInfo'])
        },
		methods: {
            ...mapMutations(['addressListSave']),
		    initAddress(){
		        var params = this.userInfo.id
                this.$http.post("address/findUserAddress?userId="+params).then((res) => {
                    console.log("success")
                    console.log(res)
                    if(res.data.status === "success"){
                        this.addressListSave(res.data.data);
                        console.log(res.data.data)
                        this.navToMyCart()
                    }else{
                        this.$api.msg(res.data.data.errMsg);
                    }
                }).catch(error => {
                    console.log(error)
                }).finally(() => {
                    //console.log("success")
                })
			},
			//选择地址
			checkAddress(item){
				if(this.source == 1){
					//this.$api.prePage()获取上一页实例，在App.vue定义
					this.$api.prePage().addressData = item;
					uni.navigateBack()
				}
			},
			addAddress(type, item){
				uni.navigateTo({
					url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
				})
			},
			//添加或修改成功之后回调
			refreshList(data, type){
				//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
				this.addressList.unshift(data);
				
				console.log(data, type);
			}
		}
	}
